.app {
  font-family: sans-serif;
  padding: 2rem;
  text-align: center;
  background: #f7fafd;
  min-height: 100vh;
}

.grid-container {
  position: relative;
  margin: 2rem auto;
  display: flex;
  justify-content: center;
}

.grid {
  display: grid;
  gap: 10px;
  position: relative;
  transition: all 0.4s ease-in-out;
}

.absolute-grid {
  position: relative;
}

.card {
  width: 60px;
  height: 60px;
  background: #4e77ff;
  color: white;
  font-size: 2rem;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  transition: transform 0.6s ease, top 0.6s ease, left 0.6s ease;
}

.card-style {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  margin: 1rem auto;
  max-width: 700px;
}

.primary-button,
.secondary-button {
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.primary-button {
  background-color: #4e77ff;
  color: white;
}

.secondary-button {
  background-color: #d4d4d4;
  color: #333;
}

.card.flipped {
  background-color: white;
  color: #000;
}
.card {
  transition: transform 1s ease;
}

